body,
body * {
  margin: 0;
  padding: 0;
  vertical-align: top;
  box-sizing: border-box;
}

body {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.app {
  height: 100vh;
  .top-row {
    width: 1000px;

    margin: 40px calc(50% - 500px);
    @media (max-width: 1100px) {
      width: 400px;
      margin: 0 calc(50% - 200px);
      padding: 20px 40px 40px 40px;
    }
    @media (max-width: 900px) {
      width: 400px;
      margin: 0 calc(50% - 200px);
      padding: 20px 40px 40px 40px;
    }
    @media (max-width: 700px) {
      width: 400px;
      margin: 0 calc(50% - 200px);
      padding: 20px 40px 40px 40px;
    }
    @media (max-width: 500px) {
      width: 100%;
      margin: 0;
      padding: 0;
    }
    @media (max-width: 375px) {
      width: 100%;
      padding: 0;
      margin: 0;
    }
  }

  .top {
    width: 800px;
    display: flex;
    flex-direction: column;
    background-color: #e41d27;
    padding: 30px 90px 30px 90px;
    margin: 40px calc(50% - 400px);

    @media (max-width: 900px) {
      width: 600px;
      flex-direction: column;
      margin: 30px calc(50% - 300px);
      padding: 30px 40px 40px 40px;
    }
    @media (max-width: 700px) {
      width: 400px;
      margin: 30px calc(50% - 200px);
      padding: 20px 40px 40px 40px;
    }

    @media (max-width: 500px) {
      width: 100%;
      margin: 0;
      //padding: 0;
    }
    // @media (max-width: 375px) {
    //   width: 100%;

    //   // padding: 20px 20px 30px 20px;
    // }

    img {
      color: white;
      margin-bottom: -35px;
      margin-left: 10px;
      filter: brightness(0) saturate(100%) invert(99%) sepia(79%) saturate(2%)
        hue-rotate(283deg) brightness(110%) contrast(100%);
      @media (max-width: 375px) {
        width: 100%;
        padding-top: 20px;
      }
    }
  }

  input {
    // font-size: 30px;
    background-color: #db4950;
    height: 40px;
  }
  input[type="search"] {
    color: white;

    margin-left: 10px;
    padding-left: 30px;
    @media (max-width: 700px) {
      font-size: 20px;
    }
    @media (max-width: 500px) {
      font-size: 15px;
    }
    @media (max-width: 375px) {
      font-size: 10px;
    }
  }
  .option {
    padding: 10px;
    background-color: #f4f1f1;
    color: black;
    padding-left: 40px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    @media (max-width: 900px) {
      width: 500px;
      margin: 0 calc(50% - 250px);
      // padding-top: 30px;
      font-size: 15px;
    }
    @media (max-width: 700px) {
      width: 300px;
      margin: 0 calc(50% - 150px);
    }
    @media (max-width: 500px) {
      // width: 100%;
      // margin: 0;

      // // margin: 0 calc(50% - 150px);
    }
    // @media (max-width: 500px) {
    //   width: 300px;
    //   margin: 0 calc(50% - 150px);
    // }
  }
  .option:hover {
    background-color: #e41d27;

    color: white;
  }

  // .op {
  //   display: flex;
  //   flex-direction: column;
  //   justify-content: center;
  //   align-items: center;
  // }

  .list {
    width: 600px;
    margin: 0 calc(50% - 300px);
    margin-top: -70px;
    @media (max-width: 700px) {
      width: 300px;
      margin: -70px calc(50% - 150px);
    }
    @media (max-width: 500px) {
      width: 200px;
      margin: -40px calc(50% - 100px);
    }
  }
  #filmai {
    list-style: none;
    // @media (max-width: 700px) {
    //   width: 100%;
    //   margin: 0;
    //   padding: 0;
    // }
  }

  .film {
    width: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 calc(50% - 400px);
    box-shadow: 10px 10px 10px grey;

    @media (max-width: 900px) {
      width: 600px;
      flex-direction: column;
      margin: 30px calc(50% - 300px);
    }
    @media (max-width: 700px) {
      width: 400px;
      margin: 30px calc(50% - 200px);
      flex-direction: column;
    }
    @media (max-width: 500px) {
      flex-direction: column;
      width: 100%;
      padding: 0;
      margin: 0;
    }

    @media (max-width: 375px) {
      width: 100%;
      padding: 0;
      margin: 0;
      // width: 300px;
      // margin: 20px calc(50% - 150px);

      // padding: 0;
    }
  }

  .film-img {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 300px;
    margin-right: 50px;
    @media (max-width: 700px) {
      width: 400px;
      // margin: 0 calc(50% - 150px);
      margin-right: 0;
      align-items: center;
      justify-content: flex-start;
    }
    @media (max-width: 500px) {
      width: 300px;
      // margin: 0 calc(50% - 150px);
      margin-right: 0;
      align-items: center;
      justify-content: flex-start;
    }
  }
  .description {
    margin-top: 10px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @media (max-width: 500px) {
      width: 200px;
      margin: 40px calc(50% - 100px);
      justify-content: flex-start;
    }
  }
  .star {
    width: 30px;
    margin: 20px;
  }
  .vote {
    opacity: 0.6;
  }
  .vote-star {
    display: flex;
    flex-direction: row;
    align-items: center;
    span {
      opacity: 0.6;
    }
  }
}
